import React, {PureComponent} from "react";
import s from "./MyPosts.module.css";
import Post from "./Post/Post";
import {Field, form, reduxForm, reset} from "redux-form";
import {maxLengthCreator, required} from "../../../utils/validators/validators";
import {Textarea} from "../../common/FormsContols/FormsControls";


class MyPosts extends PureComponent{
	
	// shouldComponentUpdate(nextProps, nextState) {
	// 	return nextProps !== this.props || nextState !== this.state; // сравнение для рендара
	// }
	
	onAddPost = (values) => {
		this.props.addPost(values.newPostText);
	};

	render() {
		return (
		  <div className={s.myPosts}>
			  <h3>My posts</h3>
			  <div className={s.newPost}>
				 <AddPostForm onSubmit={this.onAddPost}/>
			  </div>
			  <div className={s.posts}>
				  {this.props.posts.map(p => <Post
					 message={p.message}
					 likesCounts={p.likesCounts}
					 key={p.id} />)}
			  </div>
		  </div>
		);
	}
}

const maxLength10 = maxLengthCreator(10);

function AddPostForm (props) {
	return (
	  <form onSubmit={props.handleSubmit}>
		  <div>
			  <div>
				  <Field component={Textarea} name="newPostText"
							validate={[required, maxLength10]}
							placeholder="Enter your post" />
			  </div>
			  <div>
				  <button>Send</button>
			  </div>
		  </div>
	  </form>
	)
}

const afterSubmit = (result, dispatch) =>
  dispatch(reset('addPostForm'));

AddPostForm = reduxForm({form: "addPostForm", onSubmitSuccess: afterSubmit})(AddPostForm);

export default MyPosts;