import React from "react";
import s from "./Profileinfo.module.css";
import Preloader from "../../common/Preloader/Preloader";
import userPhoto from "../../../assets/img/userphoto.png";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

function Profileinfo(props) {
	if(!props.profile) {
		return <Preloader/>
	}
	
	return (
		<div className={s.profile_info}>
			<div className={s.myProfile}>
				<div className={s.ava}><img
					src={props.profile.photos.large !== null ? props.profile.photos.large : userPhoto} alt="userAvatar"/>
				</div>
				<div className={s.info}>
					<div className={s.name}><h2>{props.profile.fullName}</h2></div>
					<ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/>
					<div className={s.item}><strong>About me: </strong>{props.profile.aboutMe}</div>
					<div className={s.item}>
						<strong>Looking for a job: </strong>
						{props.profile.lookingForAJob
						  ? <string>in the search</string>
						  : <string>not in search</string>
						}
					</div>
					<div className={s.item}><strong>Looking for a job description: </strong>{props.profile.lookingForAJobDescription}</div>
					<div className={s.item}>
						<div className={s.contacts}>
							<strong>Contacts: </strong>
							<div className={s.contactsItems}>
								<div className={s.contactsItem}>{props.profile.contacts.facebook}</div>
								<div className={s.contactsItem}>{props.profile.contacts.website}</div>
								<div className={s.contactsItem}>{props.profile.contacts.vk}</div>
								<div className={s.contactsItem}>{props.profile.contacts.twitter}</div>
								<div className={s.contactsItem}>{props.profile.contacts.instagram}</div>
								<div className={s.contactsItem}>{props.profile.contacts.youtube}</div>
								<div className={s.contactsItem}>{props.profile.contacts.github}</div>
								<div className={s.contactsItem}>{props.profile.contacts.mainLink}</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}


export default Profileinfo;