import React from "react";
import s from "./Post.module.css";

function Post(props) {
	
  return (
		<div className={s.item}>
			<img src="http://www.nhic.ru/wp-content/uploads/2016/08/396f2d24539351.56335ec733642-150x150.png" alt=""/>
      <div className={s.message}>{props.message}</div>
			<div className={s.like}>
        &#10084;
			</div>
      <div className={s.likesCounts}>{props.likesCounts}</div>
		</div>
	);
}

export default Post;